import { Component, OnInit, Input } from '@angular/core';
import { Period } from '../model/period';

@Component({
  selector: 'br-routine-cell',
  templateUrl: './routine-cell.component.html',
  styleUrls: ['./routine-cell.component.scss']
})
export class RoutineCellComponent implements OnInit {
  @Input() allPeriods: Period[];
  @Input() dayOfWeek: any;
  @Input() timeOfDay: any;

  periods: Period[];

  constructor() {
  }

  ngOnInit() {
    // set list of periods of this cell in periods
    this.periods = [];
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < this.allPeriods.length; i++) {
      if (this.allPeriods[i].DayOfWeek === this.dayOfWeek && this.allPeriods[i].TimeOfDay === this.timeOfDay) {
        this.periods.push(this.allPeriods[i]);
      }
    }
  }
}
